.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.trafficlights_timer{
    width: 64px;
    height: 64px;
    display:inline-block;
    margin: 10px;
    opacity: 1;
    display: flex;
    flex-direction: column;
    background-color: aqua;
    text-align: center;
    justify-content: center;
}
.redback{
  color:red
}
.blueback{
  color:blue
}
.orageback{
  color:yellow
}