.trafficlights .bulb.red{
    background-color: red;
}
.trafficlights .bulb.blue{
    background-color: blue;
}
.trafficlights .bulb.orange{
    background-color: yellow;
}
.trafficlights .bulb {
    width: 64px;
    height: 64px;
    display:inline-block;
    border-radius: 50%;
    margin: 10px;
    opacity: .2;
}
.trafficlights .bulb.active{
    opacity: 1;
}
.trafficlights{
    display: flex;
    flex-direction: column;
}